import './App.css';
import Main from './allComponents/Router/Route';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import axios from 'axios';



function App() { 
  let dispatch = useDispatch();
  useEffect(()=>{
    axios.post('/session-check',{token:localStorage.getItem('meraToken')}).then((resp)=>{
      if(resp.data){
        dispatch({
          type:"LOGIN_HOGYA",
          payload:resp.data
        });

        console.log(resp.data)
      }

    })


  },[])
  return (
    <div className="App">
      <Main></Main>
      
      
      
    </div>
  );
}

export default App;
