import './header.css'
import { Link } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux';
export default ()=>{
    let dispatch = useDispatch();
    let user = useSelector(function(store){
  return store.userSection.currentUser;
    })
    return <div className='header'>

        <div className='myClass1'>
            <Link to='/'>Logo</Link>
            {user.id !=undefined && <span className='user-box'>Welcome,<img className='user-thumb1' src={user.pic} /><b>{user.name}</b></span>}
        </div>
        <div className='myClass2'>
        <ul className='zuni'>
            
            {  
            user.id  == undefined&&
            <>
            <li><Link to="/login">Login</Link></li>
            <li><Link to='/Sign'>Signup</Link></li>
            <li><Link to='/user'>User</Link></li>

            </>
}
            { user.id  != undefined && <li><Link to="/Createproduct">Createproduct</Link></li>}
            {user.id  !=undefined && <li><Link to='/dashboard'>Dashboard</Link></li>}
        
            {user.id  != undefined && <li><Link onClick={()=>{
                dispatch({
                    type:"Logout_Hogya"
                })
            }} to ='/login'>Logout</Link></li>}

        </ul>
        </div>
        
    </div>
}