import axios from "axios";
import { useState } from "react";
import { useEffect } from "react";
// import { useSelector } from "react-redux";
// import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";


export default function  User(props){
    // let dispatch = useDispatch();
    
    useEffect(function(){
        axios.get('/abc').then((response)=>{
            setUsers(response.data)
            console.log(response.data)
        })
    }, []) 
    let [users,setUsers] = useState([])

    // let users = useSelector(function(store){c
    //     return store.userSection.users;
    // },[]);
    return <div>
        {
            <table>
                
               {
                users.map(function(user,index ){
                    return (
                        <tr>
                            <td>{user.name}</td>
                            <td>{user.city}</td>
                            <td>{user.password}</td>
                            <td><button onClick={()=>{
                                 axios.delete('/user-delete?anc='+user.id).then(function(resp){
                                
                            setUsers(users.filter(myUser=>myUser.id != user.id));

                                console.log(resp.data);
                            });
                                // dispatch({
                                //     type:"PERSON_DELETED",
                                //     meraIndex:index,
                                // })
                            }}>Delete</button></td>
                            <Link to={"/Signup/"+user.id}>Edit</Link>
                            


                        </tr>
                    );
                })
               }
            </table>
        }
    </div>
}