import { createStore ,combineReducers} from "redux";

let intializedData = {
    users: [],
    currentUser:{},

}
    


function userSection(puranaData = intializedData, newData ){
    
    if(newData.type == "LOGIN_HOGYA"){
        puranaData.currentUser = newData.payload;

    }else if(newData.type == "Logout_Hogya"){

        puranaData.currentUser = {};
        localStorage.removeItem('meraToken');
        
}else if(newData.type== "PERSON_ADD"){
        puranaData.users.push(newData.payload)
    }else  if(newData.type  == "PERSON_DELETED"){
        puranaData.users.splice(newData.meraIndex,1 )
    }else if(newData.type == "PERSON_EDIT"){
        puranaData.users.splice(newData.meraIndex, 1);
    }
    return  {...puranaData};
   
    } 
    let initial = {
        products:[],
    }

function productSection(oldData = initial, newData){

    if(newData.type == "ADD_PRODUCT"){
        oldData.products.push(newData.payload)
    }
    return {...oldData};
}
let some = combineReducers({userSection,productSection})

export let MeraStore = createStore(some)