import './card.css'
export default(props)=>{
    return <div>
        <div class="card">
    <div class="card-image waves-effect waves-block waves-light">
      <img  id="img"  src= { props.myAd.picture || "https://materializecss.com/images/office.jpg"} />
    </div>
    <div class="card-content">
      <span class="card-title activator grey-text text-darken-4">Card Title<i class="material-icons right">{props.myAd.title}</i></span>
      <p><a href="#">This is a link</a></p>
    </div>
    <div class="card-reveal">
      <span class="card-title grey-text text-darken-4">Card Title<i class="material-icons right">close</i></span>
      <p>{props.myAd.price}</p>
    </div>
  </div>
    </div>
}